
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
name: 'tab',
components: {
ViewBox,
Tabbar,
TabbarItem
},
data () {
return {
selected: 'trip'
}
},
mounted () {
let to = this.$route.name
if (to === 'home') {
this.selected = 'home'
} else if (to === 'classify') {
this.selected = 'classify'
} else if (to === 'shopcar') {
this.selected = 'shopcar'
} else if (to === 'my') {
this.selected = 'my'
}
},
watch: {},

}



